.form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 60px 20px;
  min-height: 100vh;
  background-color: #f0f2f5;
}

.form-container {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 700px;
  padding: 30px;
  box-sizing: border-box;
}

.form-inner {
  border-radius: 10px;
  padding: 20px 10px;
  box-sizing: border-box;
}

.form-item {
  text-align: center;
  margin-bottom: 20px;
  padding: 0 10px;
}

.form-input {
  width: 100%;
  max-width: 100%;
  padding: 8px 12px;
  font-size: 16px;
  box-sizing: border-box;
}

.form-item-button {
  text-align: center;
  padding: 0 10px;
}

/* ✅ Responsive Enhancements */
@media (max-width: 768px) {
  .form-container {
    padding: 20px;
  }

  .form-inner {
    padding: 15px;
  }

  .form-input {
    font-size: 15px;
  }
}

@media (max-width: 480px) {
  .form-input {
    font-size: 14px;
  }

  .form-wrapper {
    padding: 40px 10px;
  }

  .form-container {
    padding: 15px;
  }
}
