/* Outer container centers the form */
.form-container1 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 40px 20px;
    box-sizing: border-box;
    margin-top: 100px;
}

/* Single wrapper card with max width */
.form-wrapper2 {
    width: 100%;
    max-width: 800px;
    background-color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 20px;
}

/* Full width input fields */
.ant-form-custom input {
    width: 100%;
}

/* Center the submit button */
.submit-button {
    text-align: center;
}

/* Responsive adjustments for mobile */
@media (max-width: 600px) {
    .form-container1 {
        padding: 20px 10px;
    }

    .form-wrapper2 {
        padding: 15px;
    }

    .ant-form-custom input {
        font-size: 14px;
    }
}
