.top-header {
  width: 100%;
  height: 50px;
  background-color: #1976d2;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 20px;
  box-sizing: border-box;
}

.logo-text {
  font-size: 30px;
  color: white;
  font-weight: bold;
  text-decoration: none;
  cursor: pointer;
}

.nav-item {
  font-size: 15px;
  color: white;
  text-decoration: none;
  cursor: pointer;
  font-weight: 500;
}

.auth-links {
  display: flex;
  gap: 30px;
}

/* Optional: Responsive support */
@media (max-width: 600px) {
  .top-header {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
    padding: 10px;
    gap: 10px;
  }

  .auth-links {
    flex-direction: column;
    gap: 10px;
  }
}



