/* Search Filters */
/* ================================
   Search Section Container
================================= */
.search-section {
  margin: 40px auto;
  padding: 30px;
  background: #fff;
  border-radius: 15px;
  width: 100%;
  max-width: 1300px;
  box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  overflow: hidden;
}

/* ================================
   Filter Row Styling
================================= */
.search-filters {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
}

/* ================================
   Input & Select Fields
================================= */
/* ================================
   Input & Select Fields (Updated)
================================= */
.search-filters input,
.search-filters select {
  width: 120px;
  /* Ensures same width */
  /* padding: 8px 12px; */
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  transition: border-color 0.2s ease;
  box-sizing: border-box;
}

@media (max-width: 768px) {

  .search-filters input,
  .search-filters select {
    width: 100%;
  }
}



.search-filters input:focus,
.search-filters select:focus {
  border-color: #1976d2;
  outline: none;
}

/* ================================
   Search Button
================================= */
.search-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 14px 24px;
  font-size: 1.05rem;
  font-weight: 600;
  color: white;
  background-color: #ff5e62;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
  min-width: 180px;
}

.search-btn:hover {
  background-color: #ff9966;
  transform: scale(1.05);
}

/* Optional: button spacing */
.Search-Button-Filter {
  margin-top: 25px;
}

/* ================================
   Responsive Breakpoints
================================= */

/* Tablets */
@media (max-width: 992px) {
  .search-filters {
    justify-content: center;
  }

  .search-filters input,
  .search-filters select {
    max-width: 100%;
  }

  .search-btn {
    width: auto;
    padding: 12px 20px;
  }
}

/* Mobile Devices */
@media (max-width: 768px) {
  .search-section {
    width: 95%;
    padding: 20px;
  }

  .search-filters {
    flex-direction: column;
    gap: 12px;
    align-items: stretch;
  }

  .search-filters input,
  .search-filters select,
  .search-btn {
    width: 100%;
  }
}

/* Small Devices */
@media (max-width: 480px) {
  .search-section {
    padding: 15px;
    width: 98%;
  }

  .search-btn {
    font-size: 1rem;
    padding: 12px;
  }
}





